import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
  Badge,
} from "react-bootstrap";
import Table from "./UsersTable";
import Cookies from "js-cookie";
import Axios from "axios";
import config from "../config.json";
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from "./SideNav";
import Loading from "./Loading";

export default function Categories() {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [userCount, setUserCount] = useState("");

  useEffect(() => {
    getAllCategories();
    $("#myModal").on("shown.bs.modal", function () {
      $("#myInput").trigger("focus");
    });
    return () => { };
  }, []);

  useEffect(() => {
    getUserCount();
  }, []);

  const getAllCategories = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.get(
      config.BASE_URL + "api/admin/getusers",
      {
        params: {
          token: Cookies.get("token"),
        },
      }
    );

    if (getAllCategoryRequest.status == "200") {
      getAllCategoryRequest.data.users.map((element) => {
        if (new Date(element.subscriptionEndDate).getTime() < Date.now())
          element.col4 = <div class="col4">Yok</div>;
        else
          element.col4 = (
            <div class="col4">
              {String(formatDate(element.subscriptionEndDate))} tarihinde
              bitecek.
            </div>
          );
      });
    }

    // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
    setCategories(getAllCategoryRequest.data.users);
    setTimeout(() => setShowLoading(false), 500);
  };

  const categoryHandler = (value) => {
    setValue(value.target.value);
  };

  function formatDate(date) {
    var d = new Date(date),
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;

    return [year, month, day].join("-");
  }
  const getUserCount = async (users) => {
    // const count = await Axios.get(config.serverApi + "getusercount", {
    //   token: Cookies.get("token"),
    // });
    const count = await Axios.get(config.serverApi + "getusercount", {
      params: {
        token: Cookies.get("token"),
      },
    })
      .then(function (count) {
        if (count) {
          setUserCount(count.data.users);
        }
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  if (Cookies.get("token"))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="users" />

        <Container className="wrapper">
          <Row>
            <Col>
              <h1 className="text">Kullanıcılar</h1>
            </Col>
            <Col>
              <Button variant="primary">
                Kullanıcı Sayısı
                <Badge variant="light" mt-2>
                  {categories.length}
                </Badge>
              </Button>
            </Col>
          </Row>
          <Row>
            <Col className="catData"></Col>

            <style jsx>{`
              .wrapper {

                border: 0;
                width: 80%;
                position: absolute;
                right: 0;
              }
              .text {
                margin-left: 18rem;
                margin-bottom: 4rem;
              }
              .catData {
                transform: translateY(-2.5rem);
              }
              .catButton {
                margin-bottom: 20px;
              }
              table {
                text-align: center;
              }
            `}</style>
          </Row>
          <Row>
            <Col>
              <Table categories={categories} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  else window.location.href = "/";
}
