import React from "react";
import LoginScreen from "./components/LoginScreen";
import { Switch, Route } from "react-router-dom";
import Categories from "./components/Categories";
import Videos from "./components/Videos";
import VideoParts from "./components/VideoParts";
import Admin from "./components/Admin";
import Users from "./components/Users";
import Settings from "./components/Settings";
import Prices from "./components/Prices";
const App = () => {
  return (
    <React.Fragment>
      <Switch>
        <Route path="/" exact component={LoginScreen} />
        <Route path="/twitter" component={Categories} />
        <Route path="/news" component={Videos} />
        <Route path="/indicators" component={VideoParts} />
        <Route path="/admin" component={Admin} />
        <Route path="/users" component={Users} />
        <Route path="/addnews" component={Prices} />
        <Route path="/addindicator" component={Settings} />
      </Switch>
    </React.Fragment>
  );
};

export default App;
