import React, { useEffect, useState } from "react";
import { useTable } from "react-table";
import config from "../config.json";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
  Badge,
} from "react-bootstrap";
import Axios from "axios";

export default function Table(props) {
  const data = props.categories;

  data.forEach((d) => {
    d.removeButton = (
      <Button variant="danger" onClick={() => removeUser(d.id)}>
        Kaldır
      </Button>
    );
  });

  const removeUser = async (id) => {
    await Axios.post(config.BASE_URL + "api/admin/deleteuser", {
      id: id,
    });
    window.location.reload();
  };

  const columns = React.useMemo(
    () => [
      {
        Header: "Kullanıcı adı",
        accessor: "username", // accessor is the "key" in the data
      },
      {
        Header: "Ad",
        accessor: "firstName", // accessor is the "key" in the data
      },
      {
        Header: "Soyad",
        accessor: "lastName", // accessor is the "key" in the data
      },
      {
        Header: "Email",
        accessor: "email",
      },

      {
        Header: "Telefon",
        accessor: "phone",
      },

      {
        Header: "Son Bitiş Tarihi",
        accessor: "col4",
      },

      {
        Header: "Kaldır",
        accessor: "removeButton",
      },
    ],
    []
  );

  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } =
    useTable({ columns, data });

  return (
    <div>
      <table {...getTableProps()} class="table table-striped table-bordered">
        <thead class="thead-dark">
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>{column.render("Header")}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {data.length < 1 && (
            <tr>
              <td colSpan="3">
                <center>Boş</center>
              </td>
            </tr>
          )}

          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
