import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Table from "./VideoPartTable";
import Cookies from "js-cookie";
import Axios from "axios";
import config from "../config.json";
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from "./SideNav";
import Loading from "./Loading";

export default function Videos(props) {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [allCategories, setAllCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [addCategoryObj, setAddCategoryObj] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [showAddVideoModal, setShowAddVideoModal] = useState(false);
  const [categoryId, setCategoryId] = useState("");
  useEffect(() => {
    getCategoryId();
    getAllVideoParts();
    $("#myModal").on("shown.bs.modal", function () {
      $("#myInput").trigger("focus");
    });
    return () => { };
  }, []);

  const getAllVideoParts = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.get(
      config.BASE_URL + "api/tradingview/getapplications",
      {
        params: {
          token: Cookies.get("token"),
          videoId: getCategoryId2(),
        },
      }
    );
    if (getAllCategoryRequest.status == "200") {
      console.log(getAllCategoryRequest.data.data)
      getAllCategoryRequest.data.data.map((element) => {
        element.operations = (
          <div class="col4">
            <Button
              onClick={() => approveUser(element._id)}
              type="button"
              class="btn btn-primary"
            >
              Onayla
            </Button>
            <Button
              type="button"
              onClick={() => {
                removeCategory(element._id);
              }}
              className="btn btn-danger"
            >
              Kaldır
            </Button>
          </div>
        );
      });

      // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
      setCategories(getAllCategoryRequest.data.data);
    }
    setTimeout(() => setShowLoading(false), 500);
  };

  const getTradeData = async () => {
    const getAllCategoryRequest = await Axios.get(
      config.BASE_URL + "api/tradingview/getapplications",
      {
        params: {
          token: Cookies.get("token"),
          videoId: getCategoryId2(),
        },
      }
    );
    console.log("bazen banaaa", getAllCategoryRequest);
  };

  useEffect(() => {
    getTradeData();
  }, []);

  const editCategoryModal = () => (
    <Modal show={showCategoryEditModal}>
      <Modal.Header>Kullanıcı Düzenle</Modal.Header>
      <Modal.Body>
        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">TradingView Adı</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="TradingView Adı"
            value={editCurrentCategory.title}
            onChange={(element) =>
              setEditCurrentCategory((prev) => ({
                ...prev,
                title: element.target.value,
              }))
            }
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">
              Kullanıcı Adı &nbsp;&nbsp;&nbsp;
            </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Kullanıcı Adı"
            value={editCurrentCategory.tradingViewId}
            onChange={(element) =>
              setEditCurrentCategory((prev) => ({
                ...prev,
                tradingViewId: element.target.value,
              }))
            }
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">E-mail</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="E-mail"
            value={editCurrentCategory.description}
            onChange={(element) =>
              setEditCurrentCategory((prev) => ({
                ...prev,
                description: element.target.value,
              }))
            }
          />
        </InputGroup>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => saveEditCategory()}>Kaydet</Button>
        <Button
          className="btn btn-danger"
          onClick={() => closeEditCategoryModal()}
        >
          İptal Et
        </Button>
      </Modal.Footer>
    </Modal>
  );

  const closeEditCategoryModal = () => {
    setShowCategoryEditModal(false);
  };
  const closeAddVideoModal = () => {
    setShowAddVideoModal(false);
  };
  const saveEditCategory = async () => {
    const editCategoryRequest = await Axios.post(
      config.BASE_URL + "api/admin/updatetradingview",
      {
        id: editCurrentCategory._id,
        title: editCurrentCategory.title,
        description: editCurrentCategory.description,
        tradingViewId: editCurrentCategory.tradingViewId,
      }
    );
    closeEditCategoryModal();
    getAllVideoParts();
  };

  const removeCategory = async (_id) => {
    console.log("id", _id);
    const removeCategoryRequest = await Axios.post(
      config.BASE_URL + "api/tradingview/remove",
      {
        _id: _id,
      }
    );
    closeEditCategoryModal();
    getAllVideoParts();
  };

  const approveUser = async (_id) => {
    console.log("id", _id);
    const removeCategoryRequest = await Axios.post(
      config.BASE_URL + "api/tradingview/approve",
      {
        _id: _id,
      }
    );
    closeEditCategoryModal();
    getAllVideoParts();
  };
  if (Cookies.get("token"))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="indicators" />
        {editCategoryModal()}

        <Container className="wrapper">
          <Row>
            <Col>
              <h1 className="text">VIP Üyeler</h1>
            </Col>
          </Row>
          <Row>
            <style jsx>{`
              .controlheader {
                display: flex;
              }
              .wrapper {

                border: 0;
                width: 80%;
                position: absolute;
                right: 0;
              }
              .text {
                margin-left: 18rem;
                margin-bottom: 4rem;
              }
              .catData {
                transform: translateY(-2.5rem);
              }
              .catButton {
                margin-bottom: 20px;
              }
              table {
                text-align: center;
              }
            `}</style>
          </Row>
          <Row>
            <Col>
              <Table categories={categories} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  else window.location.href = "/";

  function getCategoryId() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    setCategoryId(urlParams.get("video_id"));
    setAddCategoryObj((prev) => ({
      ...prev,
      videoId: urlParams.get("video_id"),
    }));
    return urlParams.get("video_id");
  }
  function getCategoryId2() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    return urlParams.get("video_id");
  }
}
