import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Cookies from "js-cookie";
import Axios from "axios";
import config from "../config.json";
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from "./SideNav";
import Loading from "./Loading";

export default function Categories() {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showAddModal, setShowAddModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [editPrice, setEditPrice] = useState({});
  const [addNewPrice, setAddNewPrice] = useState({});
  const [prices, setPrices] = useState([]);
  const [title, setTitle] = useState("");
  const [tradingViewId, setTradingViewId] = useState("");
  const [description, setDescription] = useState("");

  const closeAddModal = () => {
    setShowAddModal(false);
  };
  const closeEditModal = () => {
    setShowEditModal(false);
  };

  const addIndicator = async () => {
    Axios.post(config.BASE_URL + "api/admin/addtradingview", {
      title: title,
      tradingViewId: tradingViewId,
      description: description,
    })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
    window.location.reload();
  };

  if (Cookies.get("token"))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="addindicator" />

        <Container className="wrapper">
          <Row>
            <Col md-4>
              <h1 className="text">Indicator Ekle</h1>
              <Form>
                <Form.Group className="mb-3" controlId="formGroupEmail">
                  <Form.Label>Indicator Başlığı</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Başlık"
                    value={title}
                    onChange={(e) => {
                      setTitle(e.target.value);
                    }}
                  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formGroupPassword">
                  <Form.Label>Yazı</Form.Label>
                  <Form.Control
                    as="textarea"
                    placeholder="Yazı"
                    value={description}
                    onChange={(e) => {
                      setDescription(e.target.value);
                    }}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formGroupPassword">
                  <Form.Label>Indicator Linki</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Link"
                    value={tradingViewId}
                    onChange={(e) => {
                      setTradingViewId(e.target.value);
                    }}
                  />
                </Form.Group>
                <Button
                  className="btn btn-success mt-3"
                  onClick={() => addIndicator()}
                >
                  Ekle
                </Button>
              </Form>
            </Col>

            <style jsx>{`
              .wrapper {

                border: 0;
                width: 80%;
                position: absolute;
                right: 0;
              }
              .text {
                margin-left: 18rem;
                margin-bottom: 4rem;
              }
              .catData {
                transform: translateY(-2.5rem);
              }
              .catButton {
                margin-bottom: 20px;
              }
              table {
                text-align: center;
              }
              .priceContent {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2; /* number of lines to show */
                -webkit-box-orient: vertical;
              }
            `}</style>
          </Row>
        </Container>
      </div>
    );
  else window.location.href = "/";
}
