import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Cookies from "js-cookie";
import Axios from "axios";
import config from "../config.json";
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from "./SideNav";
import Loading from "./Loading";

export default function Categories() {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showLoading, setShowLoading] = useState(false);
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [link, setLink] = useState("");
  const [image, setImage] = useState(null);

  const closeAddModal = () => {
    setShowAddModal(false);
  };
  const closeEditModal = () => {
    setShowEditModal(false);
  };

  const addNews = async () => {

    const imageUpload = await uploadProfilePicture()
    Axios.post(config.BASE_URL + "api/admin/addnews", {
      title: title,
      content: content,
      link: link,
      image: imageUpload ? imageUpload : "",
    })
      .then(function (response) {
        console.log(response);
        window.location.reload();
      })
      .catch(function (error) {
        console.log(error);
      });

  };

  const uploadProfilePicture = async () => {

    if (!image) return false
    return new Promise(async (resolve, reject) => {

      try {



        const frm = new FormData();
        frm.append("sampleFile", image[0]);
        frm.append("type", "news")

        const uploadreq = await Axios.post(config.BASE_URL + "api/upload", frm, {
          headers: {
            Accept: 'application/json',
            'Content-Type': 'multipart/form-data'
          }
        })
        console.log("giriş", uploadreq.data)
        if (uploadreq.data) {
          resolve(uploadreq.data.url)
        }
        reject(false)
      }
      catch (e) {
        reject(false)
      }
    })

  }

  if (Cookies.get("token"))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="addnews" />

        <Container className="wrapper">
          <Row>
            <Col md-4>
              <h1 className="text">Yeni Haber Ekle</h1>
              <Form>
                <Form.Group className="mb-3" controlId="formGroupEmail">
                  <Form.Label>Haber Başlığı</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Başlık"
                    value={title}
                    onChange={(e) => {
                      setTitle(e.target.value);
                      console.log("title", title);
                    }}
                  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formGroupPassword">
                  <Form.Label>Haber Link</Form.Label>
                  <Form.Control
                    type="text-area"
                    placeholder="Link"
                    value={link}
                    onChange={(e) => {
                      setLink(e.target.value);
                    }}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formGroupEmail">
                  <Form.Label>Haber İçeriği</Form.Label>
                  <Form.Control
                    as="textarea"
                    placeholder="İçerik"
                    value={content}
                    onChange={(e) => {
                      setContent(e.target.value);
                    }}
                  />
                </Form.Group>
                {/* <Form.Label>Haber Logo</Form.Label>
                <Form.Control
                  type="file"
                  required
                  name="file"
                  // onChange={handleChange}
                /> */}
                <Form.Label>Haber Resmi</Form.Label>
                <Form.Control
                  type="file"
                  required
                  name="file"
                  onChange={(e) => {
                    setImage(e.target.files);
                  }}
                />
                <Button
                  className="btn btn-success mt-3"
                  onClick={() => addNews()}
                >
                  Ekle
                </Button>
              </Form>
            </Col>

            <style jsx>{`
              .wrapper {

                border: 0;
                width: 80%;
                position: absolute;
                right: 0;
              }
              .text {
                margin-left: 18rem;
                margin-bottom: 4rem;
              }
              .catData {
                transform: translateY(-2.5rem);
              }
              .catButton {
                margin-bottom: 20px;
              }
              table {
                text-align: center;
              }
              .priceContent {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2; /* number of lines to show */
                -webkit-box-orient: vertical;
              }
            `}</style>
          </Row>
        </Container>
      </div>
    );
  else window.location.href = "/";
}
